:root {
  --font-primary: "Nunito Sans", sans-serif;
}

:root {
  --bg-color: hsl(0, 0%, 98%);
  --elements-color: hsl(0, 0%, 100%);
  --text-color: hsl(200, 15%, 8%);
  --input-color: hsl(0, 0%, 52%);
  --shadow-primary: 0 1px 4px rgb(133, 133, 133, 0.3);
  --shadow-secondary: 0 2px 10px rgb(133, 133, 133, 0.2);
  --shadow-btn: 0 2px 7px rgb(133, 133, 133, 0.5);
  --hover-effect-color: hsl(0, 0%, 60%);
}

body.dark-theme {
  --bg-color: hsl(207, 26%, 17%);
  --elements-color: hsl(209, 23%, 22%);
  --text-color: hsl(0, 0%, 100%);
  --input-color: hsl(0, 0%, 100%);
  --shadow-primary: 0px 1px 8px 0px rgb(0, 0, 0, 0.1);
  --shadow-secondary: 0px 1px 15px 0px rgb(0, 0, 0, 0.1);
  --shadow-btn: 0px 1px 15px 0px rgb(0, 0, 0, 0.3);
}

html {
  font-size: 100%;
  scroll-behavior: smooth;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-primary);
  background-color: var(--bg-color);
  min-height: 100vh;
  transition: background 0.3s linear, color 0.3s linear;
}

main {
  padding: 2rem 0;
}

img {
  display: block;
}

.btn {
  background-color: var(--elements-color);
  color: var(--text-color);
  font-weight: 600;
  border-radius: 0.25rem;
  box-shadow: var(--shadow-btn);
  transition: all ease 0.5s;
  cursor: pointer;
}
.btn:hover {
  color: var(--hover-effect-color);
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

.container {
  width: 90vw;
  max-width: 75rem;
  margin: 0 auto;
}

.countries {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(270px, 1fr));
  gap: 2rem;
  justify-items: center;
}

.header {
  background-color: var(--elements-color);
  box-shadow: var(--shadow-primary);
}

.nav-menu {
  display: flex;
  justify-content: space-between;
  padding: 1.25rem 0;
}
.nav-menu__title a {
  font-size: 1rem;
  color: var(--text-color);
}
@media (min-width: 48em) {
  .nav-menu__title a {
    font-size: 1.5rem;
  }
}
.nav-menu__theme {
  font-family: inherit;
  font-size: 1rem;
  font-weight: 600;
  color: var(--text-color);
  display: flex;
  justify-content: initial;
  align-items: center;
  gap: 0.3125rem;
  background: transparent;
  border: none;
  cursor: pointer;
}
.nav-menu__theme span {
  font-size: 0.875rem;
}
@media (min-width: 48em) {
  .nav-menu__theme span {
    font-size: 1rem;
  }
}
@media (min-width: 48em) {
  .nav-menu__theme {
    font-size: 1.25rem;
  }
}

.search {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  gap: 2rem;
  margin-bottom: 2rem;
}
@media (min-width: 48em) {
  .search {
    flex-direction: row;
  }
}

.form {
  position: relative;
  color: var(--input-color);
  display: flex;
  justify-content: initial;
  align-items: center;
  flex: 1;
  max-width: 31.25rem;
}
.form__input {
  flex: 1;
  background-color: var(--elements-color);
  color: var(--text-color);
  box-shadow: var(--shadow-primary);
  padding: 1rem;
  padding-left: 3.75rem;
  border: none;
  border-radius: 0.5rem;
  outline: var(--elements-color);
}
.form__input::placeholder {
  color: var(--input-color);
  opacity: 0.8;
}
.form__icon {
  position: absolute;
  font-size: 1.25rem;
  left: 1.25rem;
}

.dropdown {
  display: flex;
  justify-content: space-between;
  flex: 1;
  align-items: center;
  font-family: inherit;
  font-weight: 600;
  position: relative;
  max-width: 11.875rem;
  border: none;
  border-radius: 0.5rem;
  padding: 1rem 1.5rem;
  cursor: pointer;
}
.dropdown, .dropdown__list {
  background-color: var(--elements-color);
  color: var(--text-color);
  box-shadow: var(--shadow-secondary);
}
.dropdown__icon {
  font-size: 1.25rem;
  transition: all ease 0.5s;
}
.dropdown__icon--active {
  transform: rotate(180deg);
}
.dropdown__list {
  position: absolute;
  width: 100%;
  top: 110%;
  right: 0;
  z-index: 1;
  border-radius: 0.5rem;
  padding: 1rem 2rem;
  opacity: 0;
  transform-origin: top;
  transform: scaleY(0);
  transition: all ease 0.5s;
}
.dropdown__list--active {
  opacity: 1;
  transform: scaleY(1);
}
.dropdown__item {
  text-align: start;
  padding: 0.5rem;
  transition: all ease 0.5s;
}
.dropdown__item:hover, .dropdown__item--active {
  color: var(--hover-effect-color);
  margin-left: 0.3125rem;
}

.card {
  width: 16.875rem;
  border-radius: 0.5rem;
  overflow: hidden;
  background-color: var(--elements-color);
  box-shadow: var(--shadow-primary);
  cursor: pointer;
}
.card__img {
  height: 12.5rem;
  object-fit: cover;
  width: 100%;
}
.card__content {
  color: var(--text-color);
  padding: 2rem 2rem 3rem;
}
.card__name {
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-bottom: 1rem;
}
.card__list {
  display: flex;
  justify-content: initial;
  flex-direction: column;
  gap: 0.5rem;
}
.card__item span {
  font-weight: 600;
}

.modal {
  position: fixed;
  top: 0;
  width: 100%;
  min-height: 100vh;
  background-color: var(--bg-color);
  color: var(--text-color);
  z-index: 10;
  visibility: hidden;
  transition: all ease-in-out 0.9s;
  transform: translateX(-93.75rem);
}
.modal--active {
  visibility: visible;
  transform: translateX(62.5rem);
  transform: scaleX(1);
}
.modal__overlay {
  padding: 1rem 0;
}
.modal--btn {
  font-family: inherit;
  font-weight: 600;
  border: none;
  color: var(--text-color);
  background-color: var(--elements-color);
  padding: 0.5rem 1rem;
  border-radius: 0.25rem;
  box-shadow: var(--shadow-btn);
  cursor: pointer;
}
.modal__back {
  display: flex;
  justify-content: initial;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 1rem;
}
.modal__back-icon {
  font-size: 1.125rem;
}
.modal__container {
  display: grid;
  gap: 1rem;
}
.modal__flag {
  width: 100%;
  max-height: 13.75rem;
  object-fit: cover;
}
.modal__name {
  margin-bottom: 1rem;
}
.modal__info {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  gap: 1rem;
}
.modal__item span {
  font-weight: 600;
}
.modal__border {
  margin-top: 1rem;
}
.modal__border-name {
  font-size: 1rem;
}